<script>
  import Web3 from "web3";
  import BigNumber from "bignumber.js";
  if (localStorage.getItem('lang')) {
    var langStorage = localStorage.getItem('lang');
    if (langStorage == 'ko') {
      var language = lang.ko;
      var currentLangName = language.nav7;
    } else if (langStorage == 'en') {
      var language = lang.en;
      var currentLangName = language.nav6;
    } else {
      var language = lang.si;
      var currentLangName = language.nav5;
    }
  } else {//
    var language = lang.si;
    var currentLangName = language.nav5;
  }
  export default {
    name: 'CrowdFund',
    data: function () {
      return {
        panelIndex: 0,
        tzIndex: "",
        mbIndex: "",
        langName: currentLangName,
        currentLang: language,
        currentAdr: "",
        web3: null,
        DbAbi: ItemAbi.abi,
        TzAbi: ItemAbi.abi2,
        dbAddress: ItemAbi.hyAddress,
        tzAddress: ItemAbi.tphyAddress,
        DbHy: "",
        TzHy: "",
        showData: {
          tzNumber: '',
          tzNum: 0,
          buyNumber: '',
          nb: 0,
          award1: 0,
          award2: 0,
          myTd: 0,
          myLink: 0,
          dhSNumber: 0,
          currRlJg: 96000,
          isOut: 0,
          accDb: 0,
          paddr1: 0,
          paddr2: 0,
          ptFh: 0,
          nbCj: 0,
          jsZe: 0,
          lowSy: 0,
          highSy: 0,
          dtx: 0,
          dtFh: 0,
          tdYj: 0,
          myZt: 0,
          rlXh: 0,
          tdSr: 0,
          svSr: 0,
        },
        settleTime: {
          nDay: 0,
          djH: 0,
          djM: 0,
          djS: 0,
          dateT: 0,
        },
      }
    },
    filters: {
      formatNum(val) {
        return Number(val).toFixed(6)
      },
      formatNum5(val) {
        return Number(val).toFixed(5)
      },
      formatNum4(val) {
        return Number(val).toFixed(4)
      },
      formatNum2(val) {
        return Number(val).toFixed(2)
      }
    },
    async created() {
      var that = this;
      if (localStorage.getItem('lang')) {
        var langStorage = localStorage.getItem('lang');
        if (langStorage == 'ko') {
          that.currentLang = lang.ko;
          that.langName = lang.ko.nav7;
        } else if (langStorage == 'en') {
          that.currentLang = lang.en;
          that.langName = lang.en.nav6;
        } else {
          that.currentLang = lang.si;
          that.langName = lang.si.nav5;
        }
      } else {//
        that.currentLang = lang.si;
        that.langName = lang.si.nav5;
      }
      this.initWow();
      //   web3
      await this.initWeb3Account();
    },
    mounted() {
    },
    methods: {
      async initWeb3Account() {
        var that = this;
        if (window.ethereum) {
          this.provider = window.ethereum;
          try {
            await window.ethereum.enable();
          } catch (error) {
          }
        } else if (window.web3) {
          this.provider = window.web3.currentProvider;
        } else {
          this.provider = new Web3.providers.HttpProvider("https://mainnet.infura.io");
          // this.provider = new Web3.providers.HttpProvider("https://ropsten.infura.io");
        }

        this.web3 = new Web3(this.provider);
        that.DbHy = new this.web3.eth.Contract(that.DbAbi, that.dbAddress, {});
        that.TzHy = new this.web3.eth.Contract(that.TzAbi, that.tzAddress, {});
        that.getInfo();
        setInterval(function () {
          that.getInfo();
        }, 3000);
        setInterval(function () {
          that.djTime();
          that.hjJc();
        }, 1000);
      },
      initWow: function () {
        var wow = new WOW({
          boxClass: 'wow',
          animateClass: 'animated',
          offset: 0,
          mobile: true,
          live: true
        });
        wow.init();
      },
      selectLang: function (event) {
        if (this.langName == event.target.dataset.lang) {
          return false
        } else {
          const langStanrd = event.target.dataset.stanrd;
          if (langStanrd === 'ko') {
            this.currentLang = lang.ko;
            this.langName = this.currentLang.nav7;
            localStorage.setItem('lang', langStanrd);
          } else if (langStanrd == 'en') {
            this.currentLang = lang.en;
            this.langName = this.currentLang.nav6;
            localStorage.setItem('lang', langStanrd);
          } else {
            this.currentLang = lang.si;
            this.langName = this.currentLang.nav5;
            localStorage.setItem('lang', langStanrd);

          }
        }
      },
      selectPanel: function (index) {
        this.panelIndex = index
      },
      arrivePos: function (id) {
        document.getElementById(id).scrollIntoView();
      },
      jumpMyData: function () {
        this.$router.push({path: `/MyData`})
      },
      getInfo: function () {
        var that = this;
        this.web3.eth.getAccounts().then(accs => {
          that.currentAdr = accs[0];
          that.getPageInfo()
        })
      },
      hjJc: function () {
        var that = this;
        var ishttps = 'https:' == document.location.protocol ? true : false;
        var domain = document.domain;
        if (ishttps) {
          that.showData.myLink = 'https://' + domain + '?r=' + web3.eth.defaultAccount;
        } else {
          var port = location.port;
          if(port){
            that.showData.myLink = 'http://' + domain+':'+port + '?r=' + web3.eth.defaultAccount;
          }else{
            that.showData.myLink = 'http://' + domain + '?r=' + web3.eth.defaultAccount;
          }

        }
        if (that.getUrlParam('r')) {
          that.showData.paddr1 = that.getUrlParam('r');
        }
      },
      getPageInfo: function () {
        var that = this;
        that.TzHy.methods.balance().call().then((res) => {
          that.showData.award1 = new BigNumber(res) / 10 ** 18;
        })
        that.TzHy.methods.balanceTotal().call().then((res) => {
          that.showData.award2 = (new BigNumber(res) / 10 ** 18) * (8 / 100);
        })
        that.DbHy.methods.getRanliao().call().then((res) => {
          that.showData.currRlJg = new BigNumber(res) / 10 ** 4;
        });
        that.TzHy.methods.usersMap(that.currentAdr).call().then((res) => {
          that.showData.paddr2 = res.paddr == 0 ? 0 : web3.toHex(res.paddr);
          that.showData.myTd = res.tuandui;
          that.showData.isOut = res.isout;
          that.showData.ptFh = new BigNumber(res.shouyi) / 10 ** 18;
          that.showData.tdYj = new BigNumber(res.yeji) / 10 ** 18;
          that.showData.dtFh = new BigNumber(res.invitshouyi) / 10 ** 18;
          that.showData.myZt = res.zhitui;
        })
        that.TzHy.methods.shouyisMap(that.currentAdr).call().then((res) => {
          that.showData.jsZe = new BigNumber(res.txshouyi) / 10 ** 18;
          that.showData.dtx = new BigNumber(res.ddayjiang) / 10 ** 18;
          that.showData.tdSr = new BigNumber(res.dtdvjiang) / 10 ** 18;
          that.showData.svSr = new BigNumber(res.dtvsjiang) / 10 ** 18;
          that.settleTime.dateT = new BigNumber(res.jstime);
          that.settleTime.nDay = that.calculateDiffTime(res.jstime);
          that.showData.tzNum = new BigNumber(res.tznum) / 10 ** 18;
          if (that.showData.tzNum >= 31) {
            that.showData.nb = 5;
          } else if (that.showData.tzNum >= 11 && that.showData.tzNum < 31) {
            that.showData.nb = 4;
          } else if (that.showData.tzNum > 0 && that.showData.tzNum < 11) {
            that.showData.nb = 3;
          } else {
            that.showData.nb = '_';
          }
          that.showData.nbCj = that.showData.nb * that.showData.tzNum - that.showData.jsZe > 0 ? that.showData.nb * that.showData.tzNum - that.showData.jsZe : 0;
          if (that.showData.tzNum > 0 && that.settleTime.nDay >= 1) {
            that.showData.lowSy = 3 * that.showData.tzNum * that.settleTime.nDay / 1000;
            that.showData.highSy = 1 * that.showData.tzNum * that.settleTime.nDay / 100;
          } else {
            that.showData.lowSy = 0;
            that.showData.highSy = 0;
          }


        })
        that.DbHy.methods.getBalanceOf().call({from: that.currentAdr}, function (err, res) {
          that.showData.accDb = new BigNumber(res) / 10 ** 4;
        })
        that.DbHy.methods.queryDbBurn().call().then((res) => {
          that.showData.rlXh = new BigNumber(res) / 10 ** 4;
        })
      },
      getDhSEG: function (e) {
        var that = this;
        this.showData.dhSNumber = e.target.value * that.showData.currRlJg;
      },
      QSEG: function (e) {
        if (e == 0) {
          this.mbIndex = 0;
          this.showData.buyNumber = 1;
          this.showData.dhSNumber = 1 * this.showData.currRlJg;
        } else if (e == 1) {
          this.mbIndex = 1;
          this.showData.buyNumber = 100;
          this.showData.dhSNumber = 100 * this.showData.currRlJg;
        } else if (e == 2) {
          this.mbIndex = 2;
          this.showData.buyNumber = 200;
          this.showData.dhSNumber = 200 * this.showData.currRlJg;
        }
      },
      buySEG: function () {
        var that = this;
        if (that.showData.buyNumber <= 1 && that.showData.buyNumber > 0) {
          var gasL = 300000;
          var gasP = 10 * 1000000000;
        } else {
          var gasL = 600000;
          var gasP = 8 * 1000000000;
        }
        if (that.showData.buyNumber == '') {//eth
          layer.open({
            type: 1,
            shade: false,
            title: false,
            time: 2000,
            closeBtn: 0,
