      addZero: function (i) {
        return i < 10 ? "0" + i : i + "";
      },
      djTime: function () {
        const that = this;
        var dateTwo = that.getLocalTime(that.settleTime.dateT);
        if (!dateTwo) {
          that.settleTime.djH = 0;
          that.settleTime.djM = 0;
          that.settleTime.djS = 0;
          return false;
        }
        var nowtime = new Date();
        var endtime = new Date(dateTwo);
        var lefttime = parseInt((endtime.getTime() - nowtime.getTime()) / 1000);
        var d = parseInt(lefttime / (24 * 60 * 60));
        var h = parseInt(lefttime / (60 * 60) % 24);
        var m = parseInt(lefttime / 60 % 60);
        var s = parseInt(lefttime % 60);
        //
        h = that.addZero(h);
        m = that.addZero(m);
        s = that.addZero(s);
        that.settleTime.djH = h;
        that.settleTime.djM = m;
        that.settleTime.djS = s;

      },
      getLocalTime: function (timestamp) {
        if (timestamp == 0) {
          return false;
        }
        var date = new Date((timestamp * 1000) + 7200 * 60 * 60 * 1000);
        var Y = date.getFullYear() + '/';
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '/';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() + ':';
        var s = date.getSeconds();
        return Y + M + D + h + m + s;
      },
      calculateDiffTime: function (start_time) {
        if (start_time == 0) {
          return 0;
        }
        if (!parseInt(start_time)) return "-";
        var endTime = Math.round(new Date() / 1000);
        var timeDiff = endTime - start_time;
        var day = parseInt(timeDiff / 86400);
        var hour = parseInt((timeDiff % 86400) / 3600);
        var minute = parseInt((timeDiff % 3600) / 60);
        day = day ? day : 0;
        // return hour>0?parseInt((hour*60+minute)/15):parseInt(minute/15);
        return day;
      },
      getUrlParam: function (name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]);
        return null;
      },
      copyNum: function () {
        var clipboard1 = new Clipboard('.copyLink');
        clipboard1.on('success', function (e) {
          if (e.text != '') {
            layer.open({
              type: 1,
              shade: false,
              title: false,
              time: 2000,
              closeBtn: 0,
